import { renderChoicePage } from './renderChoicePage';
export function renderResult(appEl, modalEl, gameProgress, 
//playingField: HTMLElement | null,
currentDate, combDate) {
    var minutModal = 0;
    var secondModal = 0;
    function getTimeModalWindow(minutModal, secondModal) {
        var endDate = new Date();
        var diffDate = endDate.getTime() - currentDate.getTime();
        minutModal = Math.floor(diffDate / 60000);
        secondModal = Math.floor((diffDate % 60000) / 1000);
        combDate = "".concat(minutModal < 10
            ? '0' + minutModal.toString()
            : minutModal.toString(), ":").concat(secondModal < 10
            ? '0' + secondModal.toString()
            : secondModal.toString());
        return combDate;
    }
    getTimeModalWindow(minutModal, secondModal);
    console.log(combDate);
    var playingField = document.querySelector('.playing-field');
    console.log(playingField);
    var modalHtml = "\n    <div class=\"wrap\">\n    <div class=\"result\">\n        <div class=\"result__img\">\n        ".concat(gameProgress === 0
        ? '<img src="static/img/выиграл.svg" alt="выиграл" />'
        : '<img src="static/img/проиграл.svg" alt="проиграл" />', "\n        </div>\n        <h2 class=\"result__title\">").concat(gameProgress === 0 ? 'Вы выиграли!' : 'Вы проиграли', "</h2>\n            <p class=\"result__timer-text\">\u0417\u0430\u0442\u0440\u0430\u0447\u0435\u043D\u043D\u043E\u0435 \u0432\u0440\u0435\u043C\u044F</p>\n            <div class=\"result__timer-time\">").concat(combDate, "</div>\n        <button class=\"button\" id = \"result__button\" type=\"submit\">\n            \u0418\u0433\u0440\u0430\u0442\u044C \u0441\u043D\u043E\u0432\u0430\n        </button>\n    </div>\n</div>\n");
    if (modalEl) {
        modalEl.innerHTML = modalHtml;
    }
    var resturtButton = document.getElementById('result__button');
    if (resturtButton) {
        resturtButton.addEventListener('click', function () {
            if (modalEl) {
                modalEl.style.display = 'none';
            }
            renderChoicePage(appEl);
        });
    }
    if (playingField) {
        playingField.style.opacity = '.3';
    }
}
