import { suitCardArray } from './cardArray';
import { coverCardArr } from './cardArray';
import { renderPlayingField } from './renderPlayingField';
import { renderResult } from './renderResult';
import { renderChoicePage } from './renderChoicePage';
import { counterTime } from './getTime';
export function PlayApp(level, appEl) {
    var min = 0;
    var sec = 0;
    var currentDate;
    var combDate;
    function getCurrentDate() {
        return (currentDate = new Date());
    }
    var id;
    //Создаём два сортированных массива
    var sortSuitCardArray = suitCardArray
        .sort(function () { return Math.random() - 0.5; })
        .slice(0, level / 2);
    sortSuitCardArray = sortSuitCardArray
        .concat(sortSuitCardArray)
        .sort(function () { return Math.random() - 0.5; });
    var sortCoverCardArr = coverCardArr.slice(0, level);
    //Создаём массив, который будет показываться
    var baseCardArr;
    baseCardArr = sortCoverCardArr;
    renderPlayingField(sortSuitCardArray, appEl);
    var modalEl = document.getElementById('modal');
    setTimeout(function () {
        id = counterTime(min, sec);
        getCurrentDate();
        console.log('таймер');
    }, 5000);
    var log = true;
    var firstCard;
    var secondCard;
    var gameProgress = level;
    function showCoverCard() {
        var suits = document.getElementById('suits');
        if (suits) {
            suits.innerHTML = "".concat(baseCardArr.join(""));
        }
        var resturtButton = document.getElementById('button-new-start');
        if (resturtButton) {
            resturtButton.addEventListener('click', function () {
                renderChoicePage(appEl);
            });
        }
        if (suits) {
            var itemCards = suits.children;
            var itemCardsArray = Array.from(itemCards);
            var _loop_1 = function (itemCard) {
                itemCard.addEventListener('click', function () {
                    var cardIndex = Number(itemCard.dataset.index);
                    if (log && cardIndex) {
                        firstCard = cardIndex;
                        gameProgress = --gameProgress;
                        baseCardArr[cardIndex] = sortSuitCardArray[cardIndex];
                        renderPlayingField(baseCardArr, appEl);
                        showCoverCard();
                    }
                    else {
                        secondCard = cardIndex;
                        gameProgress = --gameProgress;
                        baseCardArr[cardIndex] = sortSuitCardArray[cardIndex];
                        renderPlayingField(baseCardArr, appEl);
                        showCoverCard();
                        compareCard(firstCard, secondCard);
                    }
                    log = !log;
                });
            };
            for (var _i = 0, itemCardsArray_1 = itemCardsArray; _i < itemCardsArray_1.length; _i++) {
                var itemCard = itemCardsArray_1[_i];
                _loop_1(itemCard);
            }
        }
    }
    setTimeout(showCoverCard, 5000);
    function compareCard(firstCard, secondCard) {
        if (sortSuitCardArray[firstCard] === sortSuitCardArray[secondCard]) {
            baseCardArr[secondCard] = sortSuitCardArray[secondCard];
            if (gameProgress === 0) {
                ;
                modalEl.style.display = 'block';
                renderResult(appEl, modalEl, gameProgress, currentDate, combDate);
                clearInterval(id);
            }
            else {
                showCoverCard();
            }
        }
        else {
            baseCardArr = sortCoverCardArr;
            modalEl.style.display = 'block';
            renderResult(appEl, modalEl, gameProgress, currentDate, combDate);
            clearInterval(id);
            log = false;
        }
    }
}
